import axios from 'axios';
import { BACKEND_CONNECTION_URL } from '../constants';

const BASE_URL = BACKEND_CONNECTION_URL;

axios.defaults.baseURL = BASE_URL;

export const getTables = async () => {
  return await axios.get(`table/listNames`);
};

export const getTableData = async (data) => {
  return await axios.get(
    `table/data?table_name=${data.tableName}&limit=${data.limit}&skip=${data.skip}`,
  );
};

export const getLevels = async () => {
  return await axios.get(`levels`);
};

export const getComponents = async () => {
  return await axios.get(`components`);
};

export const getLevelsRefData = async (payload) => {
  return await axios.post('levels/data', payload);
};

export const getLayoutConfig = async (payload) => {
  return await axios.post('layout-config', payload);
};

export const createLayoutConfig = async (payload) => {
  return await axios.post('layout-config/bulk-create', payload);
};

export const addDataToTable = async (payload) => {
  return await axios.post('table/addDataToExisting', payload);
};

export const createTable = async (payload) => {
  const data = new FormData();
  let queryParams = '';
  data.append('file', payload.file);
  data.append('table_description', payload.table_description);
  data.append('tableName', payload.tableName);
  queryParams += `selectionType=${payload.selectionType}`;
  data.append('selectionType', payload.selectionType);
  if (payload.configID) {
    queryParams += `&config_id=${payload.configID}`;
    data.append('config_id', payload.configID);
  }
  return await axios.post(`table/fileIntoDb?${queryParams}`, data);
};

export const loginUser = async (payload) => {
  return await axios.post('authenticate', payload);
};

export const addDataToTableFile = async (payload) => {
  const data = new FormData();
  data.append('file', payload.file);
  data.append('tableName', payload.tableName);
  return await axios.post('table/addDataToExisting', data);
};

export const createBuilder = async (payload) => {
  return await axios.post(`layout-config/builder-ui/create`, payload);
};

export const getBuilder = async (payload) => {
  return await axios.post(`layout-config/builder-ui/list`, payload);
};
