import React, { useEffect, useRef, useState } from 'react';
import PageContainer from '../../../components/container/PageContainer';
import { addDataToTableFile, getTableData } from '../../../api';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination,
  Typography,
  Button,
  TableContainer,
  Box,
  Snackbar,
  Alert,
} from '@mui/material';
import styles from './tableData.module.scss';
import { useParams } from 'react-router-dom';
import { snakeToTitleCase } from '../../../utils';
import { useTranslation } from 'react-i18next';
import Input from '../../../components/forms/input';
import CustomFormLabel from '../../../components/forms/custom-elements/CustomFormLabel';

const TableData = () => {
  const params = useParams();
  const { t } = useTranslation();
  const inputFile = useRef(undefined);
  const [tables, setTables] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const [total, setTotal] = useState(undefined);
  const [file, setFile] = useState(undefined);
  const [alertMessage, setAlertMessage] = useState(undefined);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleClose = (reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setAlertMessage('');
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const getTablesData = async () => {
    const res = await getTableData({
      tableName: params.name,
      limit: rowsPerPage,
      skip: page * rowsPerPage,
    });
    if (res.data) {
      setTotal(res.data[0].total_count);
      const excludeFields = ['updated_by', 'updated_at', 'created_by', 'created_at', 'total_count'];
      const filterFields = (obj) => {
        return Object.keys(obj)
          .filter((key) => !excludeFields.includes(key))
          .reduce((acc, key) => {
            acc[key] = obj[key];
            return acc;
          }, {});
      };
      const filteredData = res.data.map((item) => filterFields(item));
      if (page === 0) {
        setTables(filteredData);
      } else {
        setTables([...tables, ...filteredData]);
      }
    }
  };

  useEffect(() => {
    if (params.name) {
      getTablesData();
    }
  }, [params.name, page, rowsPerPage]);

  if (!params.name) {
    return <></>;
  }

  function EnhancedTableHead() {
    return (
      <TableHead>
        <TableRow>
          {tables &&
            tables.length > 0 &&
            Object.keys(tables[0]).map((headCell, index) => (
              <TableCell
                key={index}
                sx={{
                  textAlign: 'center',
                  '@media only screen and (min-width: 3600px)': {
                    fontSize: '4rem',
                    lineHeight: 'normal',
                  },
                  '@media only screen and (max-width: 3600px)': {
                    fontSize: '1rem',
                    lineHeight: 'normal',
                    padding: '4px',
                  },
                  fontWeight: 'bold',
                }}
              >
                {snakeToTitleCase(headCell)}
              </TableCell>
            ))}
        </TableRow>
      </TableHead>
    );
  }

  const handleSubmit = async () => {
    if (!file) {
      return alert(t('missing_file'));
    }
    try {
      const res = await addDataToTableFile({
        file: file,
        tableName: params.name,
      });
      if (res.data) {
        setPage(0);
        getTablesData(0);
        setFile(undefined);
        setAlertMessage('Data inserted successfully.');
      }
    } catch (err) {
      return alert('Error adding data');
    }
  };

  return (
    <PageContainer title="Table Details" description="Table Details">
      {alertMessage && (
        <Snackbar
          open={true}
          autoHideDuration={6000}
          onClose={handleClose}
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        >
          <Alert onClose={handleClose} severity="success" variant="filled" sx={{ width: '100%' }}>
            {alertMessage}
          </Alert>
        </Snackbar>
      )}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
          width: '100%',
        }}
      >
        <Typography
          sx={{
            '@media only screen and (min-width: 3600px)': {
              fontSize: '5rem',
            },
            '@media only screen and (max-width: 3600px)': {
              fontSize: '1.4rem',
            },
            fontWeight: 'bold',
            whiteSpace: 'nowrap',
          }}
        >
          {snakeToTitleCase(params.name)}
        </Typography>
        {!file && (
          <Button
            color="secondary"
            variant="contained"
            size="large"
            sx={{
              '@media only screen and (min-width: 3600px)': {
                fontSize: '4rem',
                minWidth: '150px',
                mr: 'auto',
              },
              '@media only screen and (max-width: 3600px)': {
                minWidth: '40px',
                m: '0 2px',
                padding: '6px',
              },
            }}
            onClick={() => inputFile.current.click()}
          >
            {t('add_new_data')}
          </Button>
        )}
        {file && (
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              mr: 'auto',
            }}
          >
            <CustomFormLabel>{file?.name}</CustomFormLabel>
            <Button
              color="secondary"
              variant="contained"
              size="large"
              sx={{
                '@media only screen and (min-width: 3600px)': {
                  fontSize: '3.2rem',
                  minWidth: '64px',
                  maxHeight: '64px',
                },
                '@media only screen and (max-width: 3600px)': {
                  minWidth: '40px',
                  padding: '6px',
                },
                ml: '20px',
                mr: '30px',
              }}
              onClick={() => {
                if (inputFile.current) {
                  inputFile.current.value = '';
                  inputFile.current.type = 'text';
                  inputFile.current.type = 'file';
                }
                setFile(undefined);
              }}
            >
              X
            </Button>
            <Button
              color="secondary"
              variant="contained"
              size="large"
              sx={{
                '@media only screen and (min-width: 3600px)': {
                  fontSize: '4rem',
                  minWidth: '150px',
                  mr: 'auto',
                },
                '@media only screen and (max-width: 3600px)': {
                  minWidth: '40px',
                  m: '0 2px',
                  padding: '6px',
                },
              }}
              onClick={handleSubmit}
            >
              {t('upload')}
            </Button>
          </Box>
        )}
        <Box style={{ display: 'none' }}>
          <Input
            id={'file'}
            type="file"
            accept=".csv"
            onChange={(val) => setFile(val, 'file')}
            placeholder={'file'}
            inputFile={inputFile}
          />
        </Box>
        {tables && tables.length > 0 && (
          <div className={styles.scrollableContainer}>
            <TableContainer
              sx={{
                '@media only screen and (min-width: 3600px)': {
                  minWidth: 3000,
                  marginTop: '80px',
                  maxHeight: '45vh',
                },
                '@media only screen and (max-width: 3600px)': {
                  marginTop: '20px',
                  maxHeight: '65vh',
                },
                overflow: 'auto',
              }}
            >
              <Table stickyHeader>
                <EnhancedTableHead rowCount={tables.length} />
                <TableBody>
                  {tables.map((row, index) => {
                    return (
                      <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                        {Object.values(row).map((o, ind) => {
                          return (
                            <TableCell key={ind} sx={{ padding: '0px' }}>
                              <Typography
                                color="textSecondary"
                                sx={{
                                  textAlign: 'center',
                                  '@media only screen and (min-width: 3600px)': {
                                    fontSize: '4rem',
                                    lineHeight: 'normal',
                                    minWidth: 400,
                                  },
                                  '@media only screen and (max-width: 3600px)': {
                                    fontSize: '1rem',
                                    lineHeight: 'normal',
                                    padding: '0px',
                                  },
                                }}
                              >
                                {typeof o === 'number' ? o : o || '-'}
                              </Typography>
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </TableContainer>
            <TablePagination
              rowsPerPageOptions={[50]}
              component="div"
              count={total}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
            />
          </div>
        )}
      </div>
    </PageContainer>
  );
};

export default TableData;
