import React, { useRef, useState } from 'react';
import CustomTextField from '../custom-elements/CustomTextField';
import CustomFormLabel from '../custom-elements/CustomFormLabel';
import { useTranslation } from 'react-i18next';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import { SHOW_VIRTUAL_KEYBOARD } from '../../../constants';

const Input = (props) => {
  const { t } = useTranslation();
  const keyboard = useRef();
  const [isFocus, setIsFocus] = useState(undefined);
  const [layout, setLayout] = useState('default');

  const onChange = (input) => {
    props.onChange(input);
  };

  const handleShift = () => {
    const newLayoutName = layout === 'default' ? 'shift' : 'default';
    setLayout(newLayoutName);
  };

  const onKeyPress = (button) => {
    if (button === '{shift}' || button === '{lock}') handleShift();
    if (button === '{enter}') {
      setTimeout(() => {
        setIsFocus(undefined);
      }, 100);
    }
  };
  return (
    <>
      {isFocus && SHOW_VIRTUAL_KEYBOARD && props.type !== 'file' && (
        <div
          style={{
            position: 'absolute',
            inset: 0,
            width: '100vw',
            height: '100vh',
            zIndex: 2,
          }}
          onClick={() => setIsFocus(undefined)}
        />
      )}
      <div style={{ position: 'relative' }}>
        {props.label && <CustomFormLabel htmlFor={props.id}>{t(props.label)}</CustomFormLabel>}
        <CustomTextField
          id={props.id}
          variant="outlined"
          fullWidth
          inputRef={props.inputFile}
          placeholder={t(props.placeholder)}
          type={props.type}
          onFocus={() => setIsFocus(true)}
          customheight={props.customheight}
          value={props.value}
          inputProps={{ accept: props.accept }}
          onChange={(e) => {
            if (props.type === 'file') {
              props.onChange(e.target.files[0]);
            } else {
              props.onChange(e.target.value);
            }
          }}
          sx={{ ...props.textFieldStyles }}
        />
        {SHOW_VIRTUAL_KEYBOARD && isFocus && props.type !== "file" && (
          <div
            style={{
              position: 'absolute',
              top: 300,
              left: 300,
              width: '25vw',
              zIndex: 3,
            }}
          >
            <Keyboard
              keyboardRef={(r) => (keyboard.current = r)}
              layoutName={layout}
              onChange={onChange}
              onKeyPress={onKeyPress}
            />
          </div>
        )}
      </div>
    </>
  );
};
export default Input;
