import Papa from 'papaparse';

export const snakeToTitleCase = (snakeStr) => {
  return snakeStr
    .split('_') // Split the string into an array of words
    .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()) // Capitalize the first letter of each word
    .join(' '); // Join the words back into a single string with spaces
};

export const formatCSVToDataArray = (csv) => {
  const parsedData = Papa.parse(csv, {
    skipEmptyLines: true,
  });

  if (parsedData.errors.length > 0) {
    console.error('Error parsing CSV:', parsedData.errors);
    return null;
  }

  const data = parsedData.data.map((row) => {
    return row.map((cell) => ({ value: cell }));
  });

  console.timeEnd('CSV Conversion Data');
  return { parsedData, data };
};

export const formatXLSXToDataArray = (xlsxData) => {
  const workbook = xlsx.read(xlsxData, { type: 'binary' });
  const sheetName = workbook.SheetNames[0];
  const worksheet = workbook.Sheets[sheetName];

  const data = xlsx.utils
    .sheet_to_json(worksheet, { header: 1 })
    .map((row) => row.map((cell) => ({ value: cell })));

  console.timeEnd('XLSX Conversion Data');
  return { data };
};
